import React from 'react'
import { Icon } from 'antd'
// 
export const AccountThead = [
  {
    title: '',
    dataIndex: 'name'
  },
  {
    title: '',
    dataIndex: 'userName'
  },
  {
    title: '',
    dataIndex: 'phone'
  },
  {
    title: '',
    dataIndex: 'email'
  },
  {
    title: '',
    dataIndex: 'companyName'
  },
  {
    title: '',
    dataIndex: 'roleName'
  },
  {
    title: '',
    dataIndex: 'enabled',
    render: (text, record) => (
      <span className={record.enabled == true ? 'text-primary' : 'text-danger'}>{record.enabled == true ? '' : ''}</span>
    )
  },
  {
    title: '',
    dataIndex: 'createdOn',
    render: (text, record) => (
      <span>{record.createdOn ? record.createdOn.substr(0, 10) : ''}</span>
    )
  }
]

