import React from 'react'

export const rosterListThead = [
  {
    title: '',
    dataIndex: 'name'
  },
  {
    title: '',
    dataIndex: 'idCardNo'
  },
  {
    title: '',
    dataIndex: 'sex',
    key: 'sex',
    render: (text, record) => (
      <span>{record.sex === 1 ? '' : ''}</span>
    )
  },
  {
    title: '',
    dataIndex: 'phone'
  },
  {
    title: '',
    dataIndex: 'beHiring',
    render: (text, record) => (
      <span>{record.beHiring == true ? '' : ''}</span>
    )
  },
  {
    title: '',
    dataIndex: 'companyName'
  },
  {
    title: '',
    dataIndex: 'department'
  },
  {
    title: '',
    dataIndex: 'duty'
  },
  {
    title: '',
    dataIndex: 'createTime'
  }
]

