import React, {Component} from 'react'
import { Form, Row, Col, Select, Cascader, DatePicker, Input, Table, Button, message } from 'antd'
import SysAreaApi from '@/api/SysArea.api'
import EmployeesApi from '@/api/Employees.api'
import BreadcrumbCustom from '@/components/layout/BreadcrumbCustom'
import { Link, withRouter} from "react-router-dom"
import tool from '@/lib/tool'
import moment from 'moment'

const { Option } = Select
// form
const formBasicData = {
  id: '', // ID
  companyId: '', // ID
  companyName: '', // 
  insuranceId: '', // ID
  name: '',
  eName: '',
  idType: '0',
  idCardNo: '',
  otherIdCardNo: '',
  birthDay: '',
  sex: '',
  age: '',
  race: '',
  birthplace: '',
  idCardStartTime: '',
  idCardEndTime: '',
  idCardAddress: '',
  residentAddress: '',
  Phone: '',
  email: '',
  emergencyContact: '',
  emergencyContactPhone: '',
  maritalStatus: '',
  hasChildren: '',
  professionalTitle: '',
  degree: '',
  schoolName: '',
  graduationDate: '',
  discipline: '',
  onBoardingDate: '',
  department: '',
  duty: '',
  employeeNo: '',
  employeeType: '',
  employeeTypeName: '',
  beHiring: 'true',
  city: [],
  cityStr: '',
  accountName: '',
  bankName: '',
  accountNo: '',
  salaryCard: {
    accountName: '',
    bankName: '',
    accountNo: ''
  }
}

// item
const formBasicInfoItem = [
  { type: 'input', label: '', placeholder: '', model: 'name', fn: 'inputOnBlur' },
  { type: 'input', label: '', placeholder: '', model: 'eName' },
  { type: 'select', label: '', placeholder: '', model: 'idType', options: 'idcardtype', fn: 'idTypeChange' },
  { type: 'input', label: '', placeholder: '', model: 'idCardNo', fn: 'inputOnBlur' },
  { type: 'datePicker', label: '', placeholder: '', model: 'birthDay' },
  { type: 'select', label: '', placeholder: '', model: 'sex', options: 'sex' },
  { type: 'input', label: '', placeholder: '', model: 'age' },
  { type: 'input', label: '', placeholder: '', model: 'race' },
  { type: 'input', label: '', placeholder: '', model: 'birthplace' },
  { type: 'datePicker', label: '', placeholder: '', model: 'idCardStartTime' },
  { type: 'datePicker', label: '', placeholder: '', model: 'idCardEndTime' },
  { type: 'input', label: '', placeholder: '', model: 'idCardAddress' },
  { type: 'input', label: '', placeholder: '', model: 'residentAddress' },
  { type: 'input', label: '', placeholder: '', model: 'phone' },
  { type: 'input', label: 'Email', placeholder: 'Email', model: 'email' },
  { type: 'input', label: '', placeholder: '', model: 'emergencyContact' },
  { type: 'input', label: '', placeholder: '', model: 'emergencyContactPhone' },
  { type: 'select', label: '', placeholder: '', model: 'maritalStatus', options: 'maritalstatus' },
  { type: 'select', label: '', placeholder: '', model: 'hasChildren', options: 'hasChildrenOps' },
  { type: 'input', label: '', placeholder: '', model: 'professionalTitle' },
  { type: 'select', label: '', placeholder: '', model: 'degree', options: 'degree' },
  { type: 'input', label: '', placeholder: '', model: 'schoolName' },
  { type: 'datePicker', label: '', placeholder: '', model: 'graduationDate' },
  { type: 'input', label: '', placeholder: '', model: 'discipline' },
  { type: 'datePicker', label: '', placeholder: '', model: 'onBoardingDate' },
  { type: 'input', label: '', placeholder: '', model: 'companyName', isDisabled: true },
  { type: 'input', label: '', placeholder: '', model: 'department' },
  { type: 'input', label: '', placeholder: '', model: 'duty' },
  { type: 'input', label: '', placeholder: '', model: 'employeeNo' },
  { type: 'select', label: '', placeholder: '', model: 'employeeType', options: 'EmploymentForm' },
  { type: 'select', label: '', placeholder: '', model: 'beHiring', options: 'beHiringOps' },
  { type: 'cascader', label: '', placeholder: '', model: 'city', options: 'cityId' },
  { type: 'input', label: '', placeholder: '', model: 'accountName', parent: 'salaryCard', isDisabled: true },
  { type: 'input', label: '', placeholder: '', model: 'bankName', parent: 'salaryCard' },
  { type: 'input', label: '', placeholder: '', model: 'accountNo', parent: 'salaryCard' }
]

const arrListDown:any = {
  cityId: [],
  booleanJudge: [{ value: 'true', label: '' }, { value: 'false', label: '' }],
  tempOptions: [{ value: 0, label: '1' }, { value: 1, label: '2' }],
  hasChildrenOps: [{ value: 'true', label: '' }, { value: 'false', label: '' }],
  beHiringOps: [{ value: 'true', label: '' }, { value: 'false', label: '' }],
  idcardtype: [],
  sex: [],
  maritalstatus: [],
  degree: [],
  EmploymentForm: []
}

let urlParams = {}

class RosterDetail extends Component<any,any> {
  state:any = {
    basicInfo: {},
    formBasicData: {},
    canEdit: false,
    areaTree: []
  }

  constructor(props:any) {
    super(props)

    // URL
    urlParams = tool.queryUrlParams(props.location.search)

    // 
    let tempAreaTree:any = localStorage.getItem('areaTree')
    this.state.areaTree = JSON.parse(tempAreaTree)

    this.state.canEdit = urlParams['isEdit'] == 'true' ? true : false

    this.getEnum()
    this.query()
  }

  // 
  getEnum () {
    let groupName = 'employee.idcardtype,employee.sex,employee.maritalstatus,employee.degree'
    tool.getEnum(groupName, arrListDown)
    let typeName = 'EmploymentForm'
    tool.getSelectValueList(typeName, arrListDown)
  }
  
  // 
  async query () {
    let basicInfo = this.state.basicInfo
    let formBasicData = this.state.formBasicData
    var params = {
      id: urlParams['id']
    }
    let res = await EmployeesApi.getEmployee(params)
    if (res.code === 200 && res.success) {
      basicInfo = res.data
      var result = res.data
      // Object.keys(formBasicData).forEach(key => {
      //   formBasicData[key] = result[key] || '--'
      // })

      // 
      for (var item in result) {
        if (typeof result[item] === 'boolean') {
          formBasicData[item] = result[item] + ''
        } else if (typeof result[item] === 'object') {
          for (var c_item in result[item]) {
            formBasicData[c_item] = result[item][c_item] + ''
          }
        } else {
          if (item === 'city') {
            var tempCity = result[item].substring(1, result[item].length - 1)
            formBasicData[item] = tempCity.split(',')
          } else {
            formBasicData[item] = result[item] + ''
          }
        }
      }
      this.setState({
        basicInfo,
        formBasicData
      })
      console.log('this.state.formBasicData : ', this.state.formBasicData)
    }
  }

  // 
  async handleSelectChange (value, model) {
    let formBasicData = this.state.formBasicData
    formBasicData[model] = value
    await this.setState({
      formBasicData
    })
  }

  // 
  async handleInputChange (model, e) {
    let formBasicData = this.state.formBasicData
    formBasicData[model] = e.target.value
    await this.setState({
      formBasicData
    })
  }

  // 
  async handleDatePickerChange (date, dateString, model) {
    let formBasicData = this.state.formBasicData
    formBasicData[model] = dateString
    await this.setState({
      formBasicData
    })
  }
  
  // 
  async handleCascaderChange (value, selectedOptions, model) {
    let formBasicData = this.state.formBasicData
    formBasicData[model] = value
    await this.setState({
      formBasicData
    })
  }

  // 
  async handleSubmit () {
    let stateFormBasicData = this.state.formBasicData
    // obj
    formBasicInfoItem.map(item => {
      if (item.parent) { // obj
        var itemModel = item.model
        formBasicData.salaryCard[itemModel] = stateFormBasicData[item.model] // objobj
      }
    })
    stateFormBasicData.salaryCard = formBasicData.salaryCard
    this.setState({
      stateFormBasicData
    })
    console.log(' this.state.formBasicData: ', this.state.formBasicData)
    const that = this
    let res = await EmployeesApi.postEmployee(this.state.formBasicData)
    if (res.code === 200 && res.success) {
      message.success(res.message)
      setTimeout(() => {
        that.props.history.push('/app/customer/rosterList')
      }, 3000)
    } else {
      message.error(res.message)
    }
  }

  goBack () {
    this.props.history.push('/app/customer/rosterList')
  }

  render () {
    let {canEdit, formBasicData, areaTree} = this.state
    const formItemLayout = {
      labelCol: { span: 8 },
      wrapperCol: { span: 16 },
    }

    return (
      <div className={urlParams['isEdit'] == 'true' ? 'cont-wrap' : 'has-disabled cont-wrap'}>
        <div className="public-title"></div>
        <div className="base-form bg-white pl-20 pr-50 pt-30 pb-20 br-5">
          <Form {...formItemLayout}>
            {formBasicInfoItem.map(item => {
              if (item.type === 'select') {
                return (
                  <Col span={6} key={item.model}>
                    <Form.Item label={item.label}>
                      <Select allowClear placeholder={canEdit ? item.placeholder : ''} value={formBasicData[item.model]} onChange={(e:any) => this.handleSelectChange(e, item.model)} disabled={!canEdit}>
                        {arrListDown[`${item.options}`].map((ops:any) => {
                          return (
                            <Option key={ops.value} value={ops.value}>{ops.label}</Option>
                          )
                        })}
                      </Select>
                    </Form.Item>
                  </Col>
                )} else if (item.type === 'cascader') {
                  return (
                    <Col span={6} key={item.model}>
                      <Form.Item label={item.label}>
                        <Cascader options={areaTree} placeholder={canEdit ? item.placeholder : ''} value={formBasicData[item.model]} onChange={(value, selectedOptions) => this.handleCascaderChange(value, selectedOptions, item.model)} disabled={!canEdit}>
                        </Cascader>
                      </Form.Item>
                    </Col>
                  )
                } else if (item.type === 'datePicker') {
                  return (
                    <Col span={6} key={item.model}>
                      <Form.Item label={item.label}>
                        <DatePicker format="YYYY-MM-DD" placeholder={canEdit ? item.placeholder : ''} value={moment(formBasicData[item.model])} onChange={(date, dateString) => this.handleDatePickerChange(date, dateString, item.model)} disabled={!canEdit} />
                      </Form.Item>
                    </Col>
                  )
                } else {
                  return (
                    <Col span={6} key={item.model}>
                      <Form.Item label={item.label}>
                        <Input allowClear placeholder={canEdit ? item.placeholder : ''} name={item.model} value={formBasicData[item.model]} onChange={this.handleInputChange.bind(this, item.model)} disabled={!canEdit} />
                      </Form.Item>
                    </Col>
                  )
                }
            })}
          </Form>
        </div>
        
        <div className="text-center pt-20">
          {
            canEdit ?
            <span>
              <Button type="primary" onClick={() => this.handleSubmit()}></Button>
              <Button type="default" onClick={() => this.goBack()} className="ml-20"></Button>
            </span>
            :
            <Button type="default" onClick={() => this.goBack()}></Button>
            
          }
        </div>
      </div>
    )
  }
}

// export default RosterDetail
export default withRouter(RosterDetail)

